<template>
  <div class="main-container" v-show="$parent.loading" style="display: none;">
    <div class="useful-link no-print">
      <!--<a class="theme-link" href="/index2.html"></a>-->
    </div>
    <section class="section section-header">
      <div class="section-bg section-header-bg"></div>
      <div class="section-bg section-content-bg"></div>
      <div class="container">
        <header class="header">
          <div class="header-box">
            <div class="avatar wow inShow no-print">
              <img src="static/images/logo.jpg" alt="logo" class="img-responsive">
            </div>
            <h1 style="text-indent: 45px;cursor:pointer" class="name text-center wow no-print shake shake-opacity hover-stop">{{userInfo.nickname}}</h1>
            <h1 class="name text-center hide show-print-block">{{userInfo.name}}</h1>
          </div>
        </header>
        <div class="section-content">
          <div class="content-box">
            <div class="name-slogan">
              <h2 class="wow inShow no-print" data-wow-delay="0.1s">
                <span class="text-light">{{userInfo.lastName}}</span>&nbsp;{{userInfo.firstName}}
              </h2>
              <div class="description wow inShow" data-wow-delay="0.15s">{{userInfo.slogan}}</div>
            </div>
            <div class="contact-info">
              <div class="row">
                <div class="col-md-6 col-lg-3">
                  <div class="item wow inShow" data-wow-delay="0.3s">
                    <h4>性别</h4>
                    <div class="info">{{userInfo.sex}}</div>
                  </div>
                </div>
                <div class="col-md-6 col-lg-3">
                  <div class="item wow inShow" data-wow-delay="0.45s">
                    <h4>年龄</h4>
                    <div class="info">{{calcDate(userInfo.birthday)}}</div>
                  </div>
                </div>
                <div class="col-md-6 col-lg-3">
                  <div class="item wow inShow" data-wow-delay="0.5s" :href="'mailto:'+userInfo.email">
                    <h4>学历</h4>
                    <div class="info">{{userInfo.education}}</div>
                  </div>
                </div>
                <div class="col-md-6 col-lg-3">
                  <a class="item wow inShow" data-wow-delay="0.55s" :href="userInfo.website" target="_blank">
                    <h4>经验</h4>
                    <div class="info">{{userInfo.exp[0]+(calcDate(userInfo.workSince)-1)+userInfo.exp[1]+calcDate(userInfo.workSince)+userInfo.exp[2]}}</div>
                  </a>
                </div>
              </div>
              <div class="row">
                <div class="col-md-6 col-lg-3">
                  <div class="item wow inShow" data-wow-delay="0.6s">
                    <h4>Phone</h4>
                    <address class="info">{{userInfo.phone}}</address>
                  </div>
                </div>
                <div class="col-md-6 col-lg-3">
                  <div class="item wow inShow" data-wow-delay="0.65s">
                    <h4>QQ</h4>
                    <div class="info">{{userInfo.qq.number}}</div>
                  </div>
                </div>
                <div class="col-md-6 col-lg-3">
                  <a class="item wow inShow" data-wow-delay="0.7s" :href="userInfo.website" target="_blank">
                    <h4>Website</h4>
                    <div class="info">{{userInfo.website}}</div>
                  </a>
                </div>
                <div class="col-md-6 col-lg-3">
                  <a class="item wow inShow" data-wow-delay="0.75s" :href="userInfo.github" target="_blank">
                    <h4>Github</h4>
                    <div class="info">{{userInfo.github}}</div>
                  </a>
                </div> 
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section @mouseenter='changeStyle' @mouseleave='resetStyle' class="section">
      <div class="container">
        <div class="section-bg section-header-bg"></div>
        <div class="section-bg section-content-bg"></div>
        <header class="header">
          <div class="content-box">
            <h2 class="title">介绍&nbsp;/&nbsp;
              <small><i>Intro</i></small>
            </h2>
            <div class="description">介绍一些个人基本情况</div>
          </div>
        </header>
        <div class="section-content">
          <div class="intro">
            <p v-for="intro in userInfo.intro.content" v-html="intro"></p>
          </div>
          <div class="technology">
            <ul class="inline">
              <li><b>{{userInfo.intro.technology.title}}</b></li>
              <li v-for="item in userInfo.intro.technology.content">{{item}}</li>
            </ul>
          </div>
        </div>
      </div>
    </section>
    <section  @mouseenter='changeStyle' @mouseleave='resetStyle' class="section">
      <div class="container">
        <div class="section-bg section-header-bg"></div>
        <div class="section-bg section-content-bg"></div>
        <header class="header">
          <div class="content-box">
            <h2 class="title">工作经验&nbsp;/&nbsp;
              <small><i>Experience</i></small>
            </h2>
            <div class="description">不同时期简短的从业经历介绍</div>
          </div>
        </header>
        <div class="section-content">
          <div class="experience">
            <div class="item" v-for="item in userInfo.experience">
              <div class="row">
                <div class="col-md-5">
                  <div class="time">{{item.time}}</div>
                  <div class="title">{{item.title}}</div>
                  <div class="description">{{item.intro}}</div>
                </div>
                <div class="col-md-7">
                  <div class="content">{{item.description}}</div>
                  <div class="tips" v-for="tip in item.tips">
                    <b>{{tip.title}}</b>
                    <ul>
                      <li v-for="list in tip.content">{{list}}</li>
                    </ul>
                  </div>
                  <div class="technology" v-for="tech in item.technology">
                    <b>{{tech.title}}</b>
                    <ul class="inline">
                      <li v-for="techItem in tech.content">{{techItem}}</li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section  @mouseenter='changeStyle' @mouseleave='resetStyle' class="section">
      <div class="container">
        <div class="section-bg section-header-bg"></div>
        <div class="section-bg section-content-bg"></div>
        <header class="header">
          <div class="content-box">
            <h2 class="title">技能&nbsp;/&nbsp;
              <small><i>Skills</i></small>
            </h2>
            <div class="description">我所掌握的工具和技术栈</div>
          </div>
        </header>
        <div class="section-content">
          <div class="row skill">
            <div class="col-md-6" v-for="s in userInfo.skill">
              <div class="item">
                <div class="text-info">
                  <span class="num text-light">{{s.percent}}</span>{{s.name}}
                </div>
                <div class="progress">
                  <div class="progress-bar wow progressShow" :style="'width:'+s.percent"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section  @mouseenter='changeStyle' @mouseleave='resetStyle' class="section">
      <div class="container">
        <div class="section-bg section-header-bg"></div>
        <div class="section-bg section-content-bg"></div>
        <header class="header">
          <div class="content-box">
            <h2 class="title">项目经验&nbsp;/&nbsp;
              <small><i>Experience</i></small>
            </h2>
            <div class="description">近期主要做过的一些项目</div>
          </div>
        </header>
        <div class="section-content">
          <div class="experience">
            <div class="item" v-for="item in userInfo.project">
              <div class="row">
                <div class="col-md-5">
                  <div class="time">{{item.time}}</div>
                  <div class="title">{{item.title}}</div>
                  <a :href="item.intro" target="_blank" @mouseenter='addLinkStyle' @mouseleave='removeLinkStyle' class="description">{{item.intro}}</a>
                </div>
                <div class="col-md-7">
                  <div class="content">{{item.description}}</div>
                  <div class="tips" v-for="tip in item.tips">
                    <b>{{tip.title}}</b>
                    <ul>
                      <li v-for="list in tip.content">{{list}}</li>
                    </ul>
                  </div>
                  <div class="technology" v-for="tech in item.technology">
                    <b>{{tech.title}}</b>
                    <ul class="inline">
                      <li v-for="techItem in tech.content">{{techItem}}</li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section  @mouseenter='changeStyle' @mouseleave='resetStyle' class="section">
      <div class="container">
        <div class="section-bg section-header-bg"></div>
        <div class="section-bg section-content-bg"></div>
        <header class="header">
          <div class="content-box">
            <h2 class="title">常逛&nbsp;/&nbsp;
              <small><i>Usually</i></small>
            </h2>
            <div class="description">经常逛的技术或者设计相关网站</div>
          </div>
        </header>
        <div class="section-content">
          <div class="row usually">
            <div class="col-lg-4 col-md-6" v-for="u in userInfo.usually">
              <a v-if="u.link" :href="u.link" target="_blank" class="item" :title="u.link">
                <i class="fa fa-github"></i>
                <h3 class="text-light">{{u.name}}</h3>
                <p>{{u.description}}</p>
              </a>
              <div v-if="!u.link" class="item" :title="u.link">
                <i class="fa fa-github"></i>
                <h3 class="text-light">{{u.name}}</h3>
                <p>{{u.description}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section  @mouseenter='changeStyle' @mouseleave='resetStyle' class="section no-print">
      <div class="container">
        <div class="section-bg section-header-bg"></div>
        <div class="section-bg section-content-bg"></div>
        <header class="header">
          <div class="content-box">
            <h2 class="title">联系&nbsp;/&nbsp;
              <small><i>Contact</i></small>
            </h2>
            <div class="description">通过这些信息可以联系到我</div>
          </div>
        </header>
        <div class="section-content">
          <div class="contact">
            <div class="row">
              <div class="col-md-6 col-lg-3">
                <div class="item">
                  <h4>Phone</h4>
                  <div class="info">{{userInfo.phone}}</div>
                </div>
              </div>
              <div class="col-md-6 col-lg-3">
                <div class="item">
                  <h4>QQ</h4>
                  <div class="info">{{userInfo.qq.number}}</div>
                </div>
              </div>
              <div class="col-md-6 col-lg-3">
                <div class="item">
                  <h4>Location</h4>
                  <div class="info">{{userInfo.location}}</div>
                </div>
              </div>
              <div class="col-md-6 col-lg-3">
                <a class="item" :href="'mailto:'+userInfo.email">
                  <h4>Email</h4>
                  <div class="info">{{userInfo.email}}</div>
                </a>
              </div>
            </div>
          </div>
          <div class="name-slogan">
            <h2 class="wow inShow">
              <span class="text-light">{{userInfo.lastName}}</span>&nbsp;{{userInfo.firstName}}
            </h2>
            <div class="description wow inShow" data-wow-delay="0.05s">{{userInfo.slogan}}（{{userInfo.tips}}）</div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      userInfo: {
        exp: [],
        qq: {},
        intro: {
          content: [],
          technology: {
            title: "",
            content: []
          }
        },
        experience: [
          {
            tips: {
              title: "",
              content: []
            },
            technology: []
          }
        ],
        skill: [],
        project: [
          {
            tips: {
              title: "",
              content: []
            },
            technology: []
          }
        ],
        usually: []
      }
    };
  },
  created() {
    var self = this;
    let res = {
      name: "林漫",
      sex: "女",
      birthday: "1995-03-10",
      workSince: "2012-11-01",
      exp: ["毕业", "年工作", "年"],
      nickname: "一支鳗鱼",
      firstName: "man",
      lastName: "Lin",
      slogan: "热爱前端开发的程序媛一枚",
      tips: "Ctrl+p可以印本简历，打印时显示真名",
      education: "本科学位证",
      phone: 15574922220,
      location: "ShanDong·China",
      qq: {
        number: "591858556"
      },
      website: "http://79px.com",
      github: "https://github.com/eternityspring",
      email: "591858556@qq.com",
      intro: {
        content: [
          "熟悉前端项目构建，有过基于 angular的产品开发经验。",
          "喜欢开源，懂产品，会设计。可以做 node.js 后端。熟悉和习惯MVC的开发模式和基于git的版本管理。",
          "有自己的外国服务器，熟悉linux操作系统。",
          "熟悉后端语言java，使用asp.net完成商城项目。",
          "自学vue，由于有angular基础上手很快，了解也更深"
        ],
        technology: {
          title: "用过的技术栈：",
          content: [
            "html,css,js",
            "jquery",
            "bootstrap",
            "angular",
            "webpack",
            "git",
            "nginx",
            "vue",
            "node.js",
            "echarts"
          ]
        }
      },
      experience: [
        {
          time: "2016.6-2017.12",
          title: "香港莱蒙照明有限公司",
          intro: "前端实习工程师",
          description:
            "主要负责公司内部LED进出货流水页面的统计模块和流程模块的开发和维护",
          tips: [
            {
              title: "主要业绩：",
              content: [
                "熟练使用html，css，js，熟悉原生js",
                "熟练使用jquery,bootstrap开发界面",
                "参与少量后台java开发"
              ]
            }
          ],
          technology: [
            {
              title: "技术栈：",
              content: [
                "html",
                "css",
                "javascript",
                "jquery",
                "bootstrap",
                "json"
              ]
            }
          ]
        },
        {
          time: "2017.3-至今",
          title: "上海中畅数据技术有限公司",
          intro: "前端开发工程师",
          description:
            "主要负责国内知名券商国泰君安CMDB等项目的外包工作，熟练使用angular框架，理解mvc设计模式",
          tips: [
            {
              title: "主要业绩：",
              content: [
                "完整参与国泰CMDB项目的前端开发，前期开发人手不够时承受压力独立完成项目结构搭建及开发工作，与后端配合完成项目10个模块中8个项目，目前该项目一期已结项上线",
                "与后端配合开发文件分发管理系统项目的界面（一期已上线）。",
                "制作几个国泰一体化运维平台pb监控界面。",
                "参与公司自动化运维产品icube的界面开发完善及上线。",
                "参与国泰容量管理系统的后端开发（java）。"
              ]
            }
          ],
          technology: [
            {
              title: "技术栈：",
              content: [
                "node.js",
                "angular",
                "echarts",
                "json",
                "nginx",
                "bootstrap",
                "tomcat",
                "webpack"
              ]
            }
          ]
        }
      ],
      skill: [
        {
          name: "Css",
          percent: "90%"
        },
        {
          name: "JavaScript",
          percent: "75%"
        },
        {
          name: "Nodejs",
          percent: "60%"
        },
        {
          name: "MongoDB",
          percent: "60%"
        },
        {
          name: "Bootstrap",
          percent: "95%"
        },
        {
          name: "Angular",
          percent: "85%"
        },
        {
          name: "Vue",
          percent: "70%"
        }
      ],
      project: [
        {
          time: "2017.04-2017.7",
          title: "上海中畅数据技术有限公司",
          intro: "公司自主开发产品",
          description: "文件分发管理系统",
          tips: [
            {
              title: "系统主要负责功能模块：",
              content: [
                "项目结构搭建，打包及发布",
                "项目功能模块设计及权限控制设计",
                "文件分发界面全部开发工作",
                "完善需求修改调整",
                "使用git进行版本控制"
              ]
            }
          ],
          technology: [
            {
              title: "技术栈：",
              content: ["html5", "css3", "node.js", "git", "angular2", "nginx"]
            }
          ]
        },
        {
          time: "2017.08-2018.04",
          title: "国泰君安外包项目",
          intro: "http://184.170.210.136/cmdb",
          description: "国泰君安CMDB配置管理系统。",
          tips: [
            {
              title: "系统主要负责功能模块：",
              content: [
                "项目结构搭建",
                "参与项目原型图设计制作",
                "使用webpack打包发布项目",
                "项目需求分析规划",
                "模型管理，资源管理等8个模块开发",
                "参与自动发现流程规划及完整前台模块开发",
                "完善需求修改调整",
                "使用git进行版本控制"
              ]
            }
          ],
          technology: [
            {
              title: "技术栈：",
              content: [
                "html5",
                "css3",
                "node.js",
                "git",
                "angular2",
                "nginx",
                "webpack",
                "echarts",
                "bootstrap",
                "Axure"
              ]
            }
          ]
        }
      ],
      usually: [
        {
          name: "Segmentfault",
          link: "https://segmentfault.com/",
          description: "每次逛都有新发现"
        }
      ]
    };
    // this.$http.get('./static/resume.json').then((res) =>{
    // let res = './static/resume.json'
    setTimeout(() => {
      self.userInfo = res;
      this.$parent.loading = true;
    }, 0);
    // });
  },
  methods: {
    calcDate(birthday) {
      let birthdayDate = new Date(birthday);
      let todyDate = new Date();
      return todyDate.getYear() - birthdayDate.getYear();
    },
    changeStyle: function(event) {
      console.log(event);
      event.target.className += " animated pulse";
    },
    resetStyle: function(event) {
      event.target.className = "section";
    },
    addLinkStyle: function(event) {
      event.target.className += " animated tada";
      event.target.style['text-decoration'] = 'underline'
    },
    removeLinkStyle: function(event) {
      event.target.className = "description";
      event.target.style['text-decoration'] = 'none'
    }
  }
};
</script>




